//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-header: 10rem;
	--height-footer: 10rem;
	--gl-indent: 3rem;
}

html {
	height: 100%;
	// font: 400 calc(100vw / 1920 * 10)/1.33 $font_1;
	font-family: $font_1;
	font-size: 10px;
	
	@include bp($point_2, $point_6) {
		font-size: 8px;
	}
	
	@include bp($point_6, $point_7) {
		font-size: 7px;
	}

	@include bp($point_7) {
		font-size: 6px;
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	@supports (display: grid) {
		display: block;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	// overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	// padding: var(--height-header) 0 0;
	

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: none;
	}

}

.elements_list {
	padding: 3rem;
}

//------------------------------------------------------------base styles###

.cover_img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.swiper-container{
	width: 100%;
	height: 100%;
	overflow: hidden;
}